import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DeviceService } from '../../services/device.service';
import { StationService } from '../../services/station.service';
declare var AMap: any;

@Component({
  selector: 'app-device-group-detail',
  templateUrl: './device-group-detail.component.html',
  styleUrls: ['./device-group-detail.component.css']
})
export class DeviceGroupDetailComponent implements OnInit {
  map;
  info = {
      loading:false,
      offset:0,
      limit:20,
      total:0
  }
  device_list = [];
  constructor(private routerInfo: ActivatedRoute, 
              private deviceService: DeviceService,
              private stationService: StationService) { }

  ngOnInit() {
      this.searchStationDetail();
      this.search();
  }
  ngAfterViewInit(){
    this.map = new AMap.Map('map',{
        resizeEnable: true,
        zoom:16
    }); 
  }
  search(){
      const condition = {
          query:'',
          type:0,
          offset:this.info.offset,
          limit:this.info.limit,
          order_by:'created_at',
          order:'desc',
          total:this.info.total,
          station_id:this.routerInfo.snapshot.params.id
      }
      this.deviceService.search(condition).subscribe((res)=>{
          console.log(res)
          this.device_list = res['data']['list']
          for(var i =0;i<this.device_list.length;i++){
              this.addMarker(this.device_list[i])
          }
      })

  }
  stationDetail = {
      longitude:'',
      latitude:''
  }
  searchStationDetail(){
      const condition = {
          id: this.routerInfo.snapshot.params.id
      }
      this.stationService.get(condition).subscribe((res)=>{
          console.log(res)
          this.stationDetail = res['data']
          const arr = [this.stationDetail.longitude,this.stationDetail.latitude]
          this.map.setCenter(arr)
      })

  }
  changePage($event) {
    this.info.offset = ($event - 1) * this.info.limit;
    this.search();
  }
  // infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, -30)});
  //添加点标记
  addMarker(device) {
    const longitude = [device.longitude,device.latitude]
    const Device = device;
        let marker;
        switch (device.device_type_name) {
            case "烟感":
                marker = new AMap.Marker({
                    position: longitude,
                    map: this.map,
                    icon:device.status==='报警'?'../../../assets/img/smoke_gj.png':'../../../assets/img/smoke_zc.png'
                });
                break;
            
            case "智能节点":
                marker = new AMap.Marker({
                    position: longitude,
                    map: this.map,
                    icon:device.status==='报警'?'../../../assets/img/jinggai_gj.png': '../../../assets/img/jinggai_zc.png'
                });
                break;
            case "门磁":
                marker = new AMap.Marker({
                    position: longitude,
                    map: this.map,
                    icon:device.status==='报警'?'../../../assets/img/mc_gj.png' : '../../../assets/img/mc_zc.png'
                });
                break;
            case "智能锁":
                marker = new AMap.Marker({
                    position: longitude,
                    map: this.map,
                    icon: device.status==='报警'? '../../../assets/img/smoke_gj.png':'../../../assets/img/suo_zc.png'
                });
                break;
            // case "报警设备"
        }
        // const marker = new AMap.Marker({
        //     position: longitude,
        //     map: this.map
        // });
        // marker.content = '我是第' + (device.device_number) + '个Marker';
        // marker.content = `${Device.device_number}<br />
        //                   设备名:${Device.device_name} <br />
        //                   `
        // marker.on('click', this.markerClick.bind(this));
  }
  mapClick(device){
      const lng = device.longitude;
      const lat = device.latitude;
      this.map.setZoomAndCenter(13,[lng,lat])
      // this.addMarker(device);
  }
  // markerClick(e) {
  //      // this.infoWindow.setContent(e.target.content);
  //      this.infoWindow.open(this.map, e.target.getPosition());
  // }

}
